$logo: 'Museo Cyrl', sans;

$sans: 'Open Sans', sans-serif;

$gil-light: 'Gilroy-Light', sans-serif;

$gil-medium: 'Gilroy-Medium', sans-serif;

$gil-regular: 'Gilroy-Regular', sans-serif;

$gil-semibold: 'Gilroy-Semibold', sans-serif;

$lato: 'Lato', sans-serif;

$auto: 0 auto;

$purple: #5a337d;

$purple-hover: #3f225a;

html{
    width: 100%;
}

body{
    width: 100%;
    margin: 0;
    padding: 0;
    border: 0;
    font-weight: inherit;
    font-style: inherit;
    font-family: inherit;
    text-align: left;
    vertical-align: baseline;
    overflow-x: hidden;
}



a {
    text-decoration: none;
    &:hover, &:focus, &:active{
        text-decoration: none;
    }
}

input{
    outline: none;
    &:hover, &:focus, &:active{
        outline: none;
    }
}

textarea{
    outline: none;
    &:hover, &:focus, &:active{
        outline: none;
    }
}

%clearfix {
    *zoom: 1;
    &:after {
        content: '';
        display: table;
        line-height: 0;
        clear: both;
    }
}

.content-wrap {
    max-width: 1170px;
    width: 100%;
    height: 100%;
    margin: $auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

